import React from "react";

export default class Map extends React.Component {

  componentDidMount() {
    const slider = document.querySelector(".slider");

    slider.addEventListener("mousemove", function (e) {
      const beforeImage = document.querySelector(".foreground-img");
      beforeImage.style.width = `${(slider.value / 100) * 1168}px`;
    });
  }

  render() {
    const {foreground, background} = this.props;

    return (
      <div>
        <img
          id="legend_1"
          src="https://eodashboard.org/data/trilateral/WaterQuality_legend_trilateral_tsm.png"
        />
        <img
          id="legend_2"
          src="https://eodashboard.org/data/trilateral/WaterQuality_legend_trilateral.png"
        />

        <div className="background-img" style={{backgroundImage:`url(${background})`}}/>
        <div className="foreground-img" style={{backgroundImage:`url(${foreground})`}}/>
        <input
          type="range"
          min="0"
          max="100"
          defaultValue="50"
          className="slider"
          id="myRange"
        />
      </div>
    );
  }
}